.app-container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 50px 20px 80px 20px;
  }
  @media screen and (min-width: 768px) {
    .app-container {
      padding: 50px;
    }
  }
  
  .container {
    width: 90%;
    display: flex;
    flex-direction: column;
  }
  
  .image {
    width: 300px;
    height: 300px;
  }
  @media screen and (min-width: 768px) {
    .image {
      width: 700px;
      height: 400px;
    }
  }